* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #1e1e1e;
  background: linear-gradient(135deg, #e9f1fb, #fff);
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.app-container {
  text-align: center;
  background: #fff;
  border-radius: 20px;
  width: 90%;
  max-width: 450px;
  margin: 4rem auto;
  padding: 3rem 4rem;
  transition: transform .3s;
  box-shadow: 0 20px 40px #00000014;
}

.app-container:hover {
  transform: scale(1.01);
}

.home {
  text-align: center;
  padding: 2rem;
}

.title, .home-title {
  color: #000;
  margin-bottom: .5rem;
  font-size: 2.4rem;
  font-weight: 700;
}

.subtitle, .home-subtitle {
  color: #deb887;
  margin-bottom: 2rem;
  font-size: 1.1rem;
  font-style: italic;
}

.btn {
  cursor: pointer;
  color: #fff;
  background: linear-gradient(145deg, #1a73e8, #0c2e60);
  border: none;
  border-radius: 12px;
  margin: .6rem;
  padding: .9rem 2.2rem;
  font-size: 1.05rem;
  transition: all .3s;
  box-shadow: 0 8px 15px #1a73e840;
}

.btn:hover {
  background: linear-gradient(145deg, #0c2e60, #1a73e8);
  transform: translateY(-2px);
}

.tab-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 20px;
  display: flex;
}

.tab-buttons button {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background: linear-gradient(135deg, #3f51b5, #1a237e);
  border: none;
  border-radius: 16px;
  width: 250px;
  padding: 1.2rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all .3s;
  box-shadow: 0 8px 20px #3f51b54d;
}

.tab-buttons button:hover {
  background: linear-gradient(135deg, #1a237e, #3f51b5);
  transform: translateY(-3px);
  box-shadow: 0 12px 24px #1a237e59;
}

.btn-subtext {
  color: #e0e0e0;
  margin-top: .4rem;
  font-size: .9rem;
  font-weight: normal;
  display: block;
}

.home-container {
  text-align: center;
  background: linear-gradient(to bottom right, #f0f4ff, #fff);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  padding: 3rem 2rem;
  display: flex;
}

.home-container h1 {
  color: #1a237e;
  margin-bottom: 1rem;
  font-size: 2.6rem;
  font-weight: bold;
}

@media screen and (width <= 600px) {
  .home-container h1 {
    font-size: 2rem;
  }

  .subtitle {
    padding: 0 1rem;
    font-size: 1rem;
  }

  .tab-buttons {
    flex-direction: column;
    gap: 1rem;
  }

  .tab-buttons button {
    width: 100%;
    font-size: 1rem;
  }

  .btn-subtext {
    font-size: .85rem;
  }
}

.cart-list {
  padding: 0;
  list-style: none;
}

.cart-item {
  background: #f5f5f5;
  border-radius: 8px;
  margin: 12px 0;
  padding: 10px;
}

.clear-cart-btn {
  color: #fff;
  cursor: pointer;
  background-color: #f44;
  border: none;
  border-radius: 10px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
}

.clear-cart-btn:hover {
  background-color: #d33;
}

.login-page {
  background: linear-gradient(135deg, #1a73e8, #e9f1fb);
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 2rem;
  display: flex;
}

.login-card {
  backdrop-filter: blur(15px);
  color: #fff;
  text-align: center;
  background: #ffffff40;
  border: 1px solid #ffffff26;
  border-radius: 20px;
  width: 100%;
  max-width: 400px;
  padding: 3rem 2.5rem;
  box-shadow: 0 8px 32px #1f268733;
}

.input {
  color: #fff;
  backdrop-filter: blur(3px);
  background: #ffffff1a;
  border: none;
  border-radius: 12px;
  outline: none;
  width: 100%;
  margin: .6rem 0;
  padding: .8rem 1rem;
  font-size: 1rem;
}

.input::placeholder {
  color: #ccc;
}

.input:focus {
  background: #fff3;
  box-shadow: 0 0 8px #00cfff;
}

.auth-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #00cfff, #1a73e8);
  border: none;
  border-radius: 12px;
  width: 100%;
  margin-top: 1.2rem;
  padding: .9rem;
  font-size: 1.1rem;
  transition: transform .3s, box-shadow .3s;
}

.auth-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px #00cfff4d;
}

.toggle-auth {
  color: #fff;
  cursor: pointer;
  margin-top: 1.2rem;
  font-size: .95rem;
  text-decoration: underline;
}

.toggle-auth:hover {
  color: #00cfff;
}

.form-heading {
  color: #f0f0f0;
  margin-bottom: 1.4rem;
  font-size: 1.05rem;
  font-weight: 500;
}

@media screen and (width <= 480px) {
  .login-card {
    width: 90%;
    padding: 2rem 1.5rem;
  }

  .input {
    padding: .7rem .9rem;
    font-size: .9rem;
  }

  .auth-btn {
    padding: .8rem;
    font-size: 1rem;
  }

  .form-heading {
    font-size: .95rem;
  }
}

.navbar {
  color: #fff;
  background: linear-gradient(135deg, #1e3a8a, #2563eb);
  border-radius: 12px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 20px;
  padding: 1rem 2rem;
  animation: .6s ease-out slideIn;
  display: flex;
  box-shadow: 0 4px 12px #0000001a;
}

.logo {
  align-items: center;
  gap: 10px;
  margin-right: 40px;
  font-size: 24px;
  font-weight: bold;
  display: flex;
}

.navbar-center {
  text-align: center;
  flex-grow: 1;
  min-width: 220px;
  margin-top: 8px;
  line-height: 1.4;
}

.navbar-center .greeting {
  color: #f1f5f9;
  font-size: 16px;
  font-weight: 600;
}

.navbar-center .stat {
  color: #cbd5e1;
  margin-top: 2px;
  font-size: 14px;
}

.nav-links {
  flex-wrap: wrap;
  align-items: center;
  gap: 1.5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-links a {
  color: #fff;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s ease-in-out;
  position: relative;
}

.nav-links a:after {
  content: "";
  background: #a5f3fc;
  width: 0%;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.nav-links a:hover {
  color: #a5f3fc;
  transform: scale(1.05);
}

.nav-links a:hover:after {
  width: 100%;
}

.nav-btn {
  color: #fff;
  cursor: pointer;
  font: inherit;
  background: none;
  border: none;
  padding: 0;
  transition: all .3s;
}

.nav-btn:hover {
  color: #a5f3fc;
  transform: scale(1.05);
}

.icon-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  padding: 6px;
  transition: background-color .2s, transform .2s;
}

.icon-btn:hover {
  background-color: #ffffff1a;
  transform: scale(1.1);
}

.dark-theme {
  color: #f8fafc;
  background-color: #0f172a;
}

@media (width <= 600px) {
  .navbar {
    text-align: center;
    flex-direction: column;
    gap: 1rem;
  }

  .nav-links {
    flex-direction: column;
    gap: .8rem;
  }

  .navbar-center {
    margin-top: 10px;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.info-panel {
  backdrop-filter: blur(6px);
  background: #f1f5f9e6;
  border: 1px solid #cbd5e166;
  border-radius: 16px;
  width: 100%;
  margin: 24px 0;
  padding: 24px;
  transition: all .3s;
  box-shadow: 0 8px 24px #00000014;
}

.info-panel.dark {
  color: #f8fafc;
  background: #1e293bcc;
  border: 1px solid #94a3b833;
}

.info-top {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.greeting {
  color: #1e3a8a;
  font-size: 1.75rem;
  font-weight: 700;
}

.info-panel.dark .greeting {
  color: #38bdf8;
}

.actions {
  align-items: center;
  gap: 12px;
  display: flex;
}

.theme-btn, .notify-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(to right, #3b82f6, #2563eb);
  border: none;
  border-radius: 10px;
  padding: 10px 16px;
  font-size: .9rem;
  font-weight: 500;
  transition: all .3s ease-in-out;
  box-shadow: 0 2px 8px #0000001a;
}

.theme-btn:hover, .notify-btn:hover {
  background: linear-gradient(to right, #2563eb, #1d4ed8);
  transform: translateY(-2px);
}

.stats {
  color: #334155;
  flex-direction: column;
  gap: 10px;
  margin-top: 18px;
  font-size: 1.1rem;
  display: flex;
}

.stat {
  background: #e2e8f0;
  border-radius: 10px;
  align-items: center;
  gap: 12px;
  padding: 10px 14px;
  transition: background .3s;
  display: flex;
}

.stat:hover {
  background: #cbd5e1;
}

.info-panel.dark .stat {
  color: #f1f5f9;
  background: #334155;
}

.info-panel.dark .stat:hover {
  background: #475569;
}

@media (width <= 600px) {
  .greeting {
    font-size: 1.4rem;
  }

  .theme-btn, .notify-btn {
    padding: 8px 12px;
    font-size: .85rem;
  }

  .stat {
    font-size: 1rem;
  }
}

body.dark-theme {
  background-color: #0f172a;
}

.profile-container {
  background: linear-gradient(to right, #dbeafe, #f0f9ff);
  justify-content: center;
  align-items: center;
  min-height: 85vh;
  padding: 2rem;
  display: flex;
}

.profile-card {
  text-align: center;
  background: #fff;
  border-radius: 20px;
  width: 100%;
  max-width: 480px;
  padding: 2.5rem 3rem;
  transition: transform .3s, box-shadow .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 25px #00000014;
}

.profile-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 16px 32px #0000001f;
}

.profile-avatar {
  color: #1e40af;
  background: #e0e7ff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 90px;
  margin-bottom: 1.2rem;
  font-size: 2.5rem;
  font-weight: bold;
  display: inline-flex;
}

.profile-card h2 {
  color: #1d4ed8;
  margin-bottom: 1rem;
  font-size: 1.9rem;
}

.profile-card p {
  color: #374151;
  margin: .4rem 0;
  font-size: 1.05rem;
}

.profile-divider {
  border-top: 1px solid #e5e7eb;
  width: 80%;
  margin: 1.5rem auto;
}

.badge {
  color: #0369a1;
  background-color: #e0f2fe;
  border-radius: 12px;
  margin-top: .8rem;
  padding: .4rem .8rem;
  font-size: .9rem;
  display: inline-block;
}

@media (width <= 500px) {
  .profile-card {
    padding: 2rem;
  }

  .profile-card h2 {
    font-size: 1.5rem;
  }

  .profile-avatar {
    width: 70px;
    height: 70px;
    font-size: 2rem;
  }

  .badge {
    padding: .3rem .6rem;
    font-size: .8rem;
  }
}

